import "./NavSecondPage.scss";

import React from "react";
import { Link } from "../Link";
import { ArrowLeftIcon, ArrowRightIcon } from "../Svg";



export function NavSecondPagePreviousTo({ className, linkTitle, linkTo, }) {
  let clsName = "";
  if (className) clsName += ` ${className}`;
  return (
    <Link className={clsName} to={linkTo}>
      <ArrowLeftIcon />
      <span>{linkTitle}</span>
    </Link>
  );
};


export function NavSecondPageNextTo({ className, linkTitle, linkTo, }) {
  let clsName = "";
  if (className) clsName += ` ${className}`;
  return (
    <Link className={clsName} to={linkTo}>
      <span>{linkTitle}</span>
      <ArrowRightIcon />
    </Link>
  );
};


export function NavSecondPageBackTo({ className, linkTitle, linkTo, }) {
  let clsName = "back second-page__nav-link";
  if (className) clsName += ` ${className}`;
  return (
    <NavSecondPagePreviousTo
      className={clsName}
      linkTitle={linkTitle}
      linkTo={linkTo}
    />
  );
};


export function NavSecondPagePreviousNext({
  className,
  previous = { className: "", title: "", linkTitle: "", linkTo: "", },
  next = { className: "", title: "", linkTitle: "", linkTo: "", },
}) {
  let clsName = "";
  if (className) clsName += ` ${className}`;
  return (
    <div className="second-page__nav">
      <div className="second-page__nav-left second-page__nav-item">
        <span className="second-page__nav-title">{next.title}</span>
        <div className="second-page__nav-link">
          <NavSecondPagePreviousTo
            className={previous.className}
            linkTitle={previous.linkTitle}
            linkTo={previous.linkTo}
          />
        </div>
      </div>

      <div className="second-page__nav-right second-page__nav-item">
        <span className="second-page__nav-title">{next.title}</span>
        <div className="second-page__nav-link">
          <NavSecondPageNextTo
            className={next.className}
            linkTitle={next.linkTitle}
            linkTo={next.linkTo}
          />
        </div>
      </div>
    </div>
  );
};
